.cartContainer {
    min-height: 73vh;
}

.cart {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

.cartItems {
    width: 70%;
}

@media (max-width: 900px) {
    .cartItems {
        width: 85%;
    }
}

@media (max-width: 500px) {
    .cartItems {
        width: 95%;
    }
}

.checkout button {
    width: 150px;
    height: 50px;
    background-color: rgb(19, 19, 19);
    color: white;
    border: none;
    border-radius: 8px;
    margin: 10px;
    cursor: pointer;
}

.checkout button:hover {
    opacity: 90%;
}